:root {
  --grey-light: #f0f0f1;
  --grey-dark: #c3c4c7;
}

/* CONTAINER */
/*----------------------------------------------------------------------------*/
.JW_ACA--l-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

/* CARD */
/*----------------------------------------------------------------------------*/
.JW_ACA--c-card {
  font-family: unset !important;
  font-size: unset !important;
  line-height: unset !important;
  display: flex;
  flex-direction: column;
  border: 1px solid var(--grey-dark);
}
.JW_ACA--c-card__header {
  margin: 0 !important;
  padding: 0 !important;
  border: none !important;
  border-bottom-right-radius: 0 !important;
  border-bottom-left-radius: 0 !important;
  border-top-right-radius: 0 !important;
  border-top-left-radius: 0 !important;
  display: flex !important;
  flex-direction: column;
  align-items: center;
  padding-left: 4px !important;
  padding-right: 4px !important;
  background: var(--grey-light) !important;
}
.JW_ACA--c-card__body, .JW_ACA--c-card__body--flush {
  margin: 0 !important;
  padding: 0 !important;
  border: none !important;
  border-bottom-right-radius: 0 !important;
  border-bottom-left-radius: 0 !important;
  border-top-right-radius: 0 !important;
  border-top-left-radius: 0 !important;
  overflow: unset !important;
  display: flex;
  flex-direction: column;
  padding-left: 4px !important;
  padding-right: 4px !important;
  background: white;
}
.JW_ACA--c-card__body--flush {
  padding: 0 !important;
}
.JW_ACA--c-card__title {
  margin: 0 !important;
  font-weight: 600;
}

/* ICON */
/*----------------------------------------------------------------------------*/
.JW_ACA--c-icon {
  position: unset !important;
  text-indent: unset !important;
  overflow: unset !important;
  background-image: unset !important;
  background-repeat: unset !important;
  margin-top: unset !important;
  width: unset !important;
  height: unset !important;
}

/* TABLE */
/*----------------------------------------------------------------------------*/
.JW_ACA--c-table {
  width: 100%;
  border-collapse: collapse;
}
.JW_ACA--c-table--xs td {
  padding: 4px;
  vertical-align: middle;
  line-height: 1em;
  font-size: 1em;
}
.JW_ACA--c-table tr {
  border-top: 1px solid var(--grey-light);
}
.JW_ACA--c-table td code {
  padding: 2px;
}
.JW_ACA--c-table td img {
  margin: 0;
}

/* TOOLTIP */
/*----------------------------------------------------------------------------*/
.JW_ACA--c-tooltip {
  display: none;
  padding-top: 4px !important;
  padding-bottom: 4px !important;
  padding-left: 8px !important;
  padding-right: 8px !important;
  position: absolute;
  z-index: 9999;
  max-width: 300px;
  color: white;
  background: #1d2327;
  border-radius: 4px;
  text-align: center;
  font-size: 11px !important;
  /* Arrow []> */
}
.JW_ACA--c-tooltip * {
  font-size: 11px !important;
}
.JW_ACA--c-tooltip p {
  margin-top: 0 !important;
}
.JW_ACA--c-tooltip p:last-child {
  margin-bottom: 0 !important;
}
.JW_ACA--c-tooltip::after {
  content: "";
  position: absolute;
  display: block;
  width: 0px;
  right: 0;
  top: 50%;
  border: 5px solid transparent;
  border-right: 0;
  border-left: 5px solid #1d2327;
  transform: translate(100%, -50%);
}

/* Border */
/*----------------------------------------------------------------------------*/
.JW_ACA--u-border--top {
  border-right: unset !important;
  border-left: unset !important;
  border-bottom: unset !important;
}
.JW_ACA--u-border--add-all {
  border: solid 1px var(--grey-dark) !important;
}
.JW_ACA--u-border--add-top {
  border-top: solid 1px var(--grey-dark) !important;
}
.JW_ACA--u-border--add-bottom {
  border-bottom: solid 1px var(--grey-dark) !important;
}
.JW_ACA--u-border--add-left {
  border-left: solid 1px var(--grey-dark) !important;
}
.JW_ACA--u-border--add-right {
  border-right: solid 1px var(--grey-dark) !important;
}
.JW_ACA--u-border--remove-all {
  border: none !important;
}
.JW_ACA--u-border--remove-top {
  border-top: none !important;
}
.JW_ACA--u-border--remove-bottom {
  border-bottom: none !important;
}
.JW_ACA--u-border--remove-left {
  border-left: none !important;
}
.JW_ACA--u-border--remove-right {
  border-right: none !important;
}
.JW_ACA--u-border--width-4 {
  border-width: 4px !important;
}
.JW_ACA--u-border--color-grey-dark {
  border-color: var(--grey-dark) !important;
}

/* DISPLAY */
/*----------------------------------------------------------------------------*/
.JW_ACA--c-display--flex {
  display: flex;
}

/* Flex */
/*----------------------------------------------------------------------------*/
.JW_ACA--u-flex--direction-row {
  flex-direction: row;
}
.JW_ACA--u-flex--direction-row-reverse {
  flex-direction: row-reverse;
}
.JW_ACA--u-flex--align-items-center {
  align-items: center;
}
.JW_ACA--u-flex--justify-content-between {
  justify-content: space-between;
}

/* Font */
/*----------------------------------------------------------------------------*/
.JW_ACA--u-font--size-xs {
  font-size: 0.8em;
}
.JW_ACA--u-font--size-sm {
  font-size: 0.9em;
}
.JW_ACA--u-font--size-md {
  font-size: 1em;
}
.JW_ACA--u-font--size-lg {
  font-size: 1.25em;
}
.JW_ACA--u-font--size-xl {
  font-size: 1.5em;
}

/* Gap */
/*----------------------------------------------------------------------------*/
.JW_ACA--u-gap--row-xs {
  row-gap: 4px;
}
.JW_ACA--u-gap--row-sm {
  row-gap: 8px;
}
.JW_ACA--u-gap--row-md {
  row-gap: 16px;
}
.JW_ACA--u-gap--row-lg {
  row-gap: 32px;
}
.JW_ACA--u-gap--row-xl {
  row-gap: 64px;
}
.JW_ACA--u-gap--column-no {
  column-gap: 0px;
}
.JW_ACA--u-gap--column-xs {
  column-gap: 4px;
}
.JW_ACA--u-gap--column-sm {
  column-gap: 8px;
}
.JW_ACA--u-gap--column-md {
  column-gap: 16px;
}
.JW_ACA--u-gap--column-lg {
  column-gap: 32px;
}
.JW_ACA--u-gap--column-xl {
  column-gap: 64px;
}

/* Padding */
/*----------------------------------------------------------------------------*/
.JW_ACA--u-padding--all-no {
  padding: 0 !important;
}
.JW_ACA--u-padding--all-xs {
  padding: 4px !important;
}
.JW_ACA--u-padding--all-sm {
  padding: 8px !important;
}
.JW_ACA--u-padding--all-md {
  padding: 12px !important;
}

/* Size */
/*----------------------------------------------------------------------------*/
.JW_ACA--u-size--12px {
  width: 12px;
  height: 12px;
}
.JW_ACA--u-size--13px {
  width: 13px;
  height: 13px;
}
.JW_ACA--u-size--width-33 {
  width: 33%;
}
.JW_ACA--u-size--width-40 {
  width: 40%;
}
.JW_ACA--u-size--width-50 {
  width: 50%;
}
.JW_ACA--u-size--width-100 {
  width: 100%;
}
.JW_ACA--u-size--height-min-20px {
  min-height: 20px !important;
}

/* Text */
/*----------------------------------------------------------------------------*/
.JW_ACA--u-text--align-left {
  text-align: left !important;
}
.JW_ACA--u-text--align-right {
  text-align: right !important;
}

/* Boolean */
/*----------------------------------------------------------------------------*/
.JW_ACA--h-boolean--true {
  color: darkgreen;
}
.JW_ACA--h-boolean--false {
  color: darkred;
}